.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

textarea {
width: 100%;
height: 10em;
}

button {
padding: 1em;
margin: 0.5em;
background: darkorange;
border: none;
}

nav {
display: flex;
justify-content: space-between;
}

.link,.link-active {
color: inherit;
text-decoration: inherit;
border: solid 1px orange;
padding: 1em;
display: block;
}
.link-active {
background: orange;
}

pair-div {
text-align: center;
border: solid 1px orange;
display: block;
}

pair-div pair-front, pair-div pair-back {
font-family: serif;
font-size: 3em;
display: block;
}
